<template>
  <div>
    <br>
    <h3>付刚的项目</h3>
    <div v-for="project in projectList" 
        :key="project.id"
        @click="link('/'+project.type+'/'+project.id)">
      <br>
      <div class="card border-secondary">
        <div class="card-body">
          <h4 class="card-title">{{project.title}}</h4>
          <div class="card-subtitle mb-2 text-muted">{{project.time}}</div>
          <p class="card-text">{{project.info}}</p>
          <img style="width: 100%; display: block;" :src="'/static/images/project'+project.id+'-0.jpg'"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    link: function(to) {
      this.$router.push(to);
    }
  },
  data() {
    return {
      projectList: []
    };
  },
  created() {
    const myList = this.GLOBAL;
    this.projectList = myList.filter(l => l.type === "projects").reverse();
  }
};
</script>



